<template>
  <v-card
    class="mt-1"
    elevation="0"
  >
    <v-card-text>
      <h1>{{ lastName.trim() }} {{ firstName.trim() }}</h1>
      <p class="mt-2">
        {{ addressLine1 }}
        <template v-if="addressLine2">
          {{ addressline2 }}<br>
        </template>

        {{ city }} {{ plz }}<br>
        <span class="font-weight-bold">Email: </span>{{ email }}<br>
        <span class="font-weight-bold">Phone: </span>{{ tel }}<br>
        <span class="font-weight-bold">Lieferzeit: </span>{{ date }} {{ time }}<br>
        {{ note }}
      </p>
      <v-chip v-if="deliveryMethod">
        {{ deliveryMethod }}
      </v-chip>
      <v-chip v-if="reason">
        {{ reason }}
      </v-chip>
    </v-card-text>
  </v-card>
</template>

<script>
  const DefaultAddressInfo = {
    reason: '',
    addressLine1: '',
    addressline2: '',
    city: '',
    plz: '',
    tel: '',
    email: '',
  }
  export default {
    name: 'AddressInfoBlock',
    props: {
      addressInfo: {
        type: Object,
        default: () => DefaultAddressInfo,
      },
    },
    data: function () {
      return Object.assign({}, DefaultAddressInfo, this.addressInfo)
    },
  }
</script>

<style scoped>

</style>
