import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/base'
import './plugins/chartist'
import './plugins/vee-validate'
import './plugins/vue-world-map'
import vuetify from './plugins/vuetify'
import i18n from './i18n'
import Print from 'vue-print-nb'
import { findAccessRight } from './components/Utlis/api'
import GlobalConfig, { loadConfig } from './GlobalSettings'
import { IKUtils } from 'innerken-js-utils'
import './registerServiceWorker'

Vue.use(Print)

const Password = IKUtils.getQueryString('Password') ?? IKUtils.getQueryString('Password') ?? ''
store.commit('SET_PASSWORD', Password)

Vue.filter('priceDisplay',
  function (price) {
    return parseFloat(price).toFixed(2).replace('.', ',')
  },
)

async function initial () {
  await loadConfig()

  store.commit('SAVE_CURRENT_DEVICE', parseInt(GlobalConfig.DeviceId))

  let accessLevel = 1
  if (Password) {
    const accessArr = (await findAccessRight(Password)).content
    // 老板权限1， 跑堂0， 兼职-1
    if (accessArr.permission === '0') {
      accessLevel = accessArr.isPartTime === '0' ? 0 : -1
    }
  }
  store.commit('SET_ACCESS_LEVEL', accessLevel)
  new Vue({
    router,
    store,
    vuetify,
    i18n,
    render: h => h(App),
  }).$mount('#app')
}

initial()
