import { loadBaseConfig } from 'aaden-base-model/lib/Models/GlobalSettings'

const defaultConfig = require('@/assets/config.json')
export let NeededKeys = []
const GlobalConfig = Object.assign({
  isShowOnTable: localStorage.getItem('isShowOnTable') || '0',
  UseDailyZbon: localStorage.getItem('UseDailyZbon') || '0',
}, defaultConfig)

export async function loadConfig (overwrite) {
  Object.assign(GlobalConfig, await loadBaseConfig(Object.assign(GlobalConfig, overwrite)))
  NeededKeys = GlobalConfig.neededKeys
  window.Config = GlobalConfig
}

export function changeLanguage (l) {
  console.log(l)
  GlobalConfig.updateSettings('lang', l)
  window.location.reload(true)
}

export default GlobalConfig
