<template>
  <v-alert
    elevation="2"
  >
    <v-spacer class="my-2"/>
    <template
      v-for=" dish in dishes"
    >
      <small-dish
        v-if="!renderList"
        :key="dish.dishesId"
        :dish-order-detail="dish"
      />
      <dish-line
        v-else
        :key="dish.dishesId"
        :dish-order-detail="dish"
      />
    </template>
  </v-alert>
</template>

<script>
  import SmallDish from './SmallDish'
  import DishLine from './DishLine'

  export default {
    name: 'DishInfoBlock',
    components: { DishLine, SmallDish },
    props: {
      dishOrders: { default: () => [], type: Array },
      renderList: { default: false, type: Boolean },
    },
    data: function () {
      return {
        dishes: this.dishOrders,
      }
    },
    watch: {
      dishes: function () {

      },
    },
  }
</script>

